import React from 'react';
import { useSelector, useDispatch } from 'react-redux';

import { addItems } from '../redux/slices/cartSlice';

const BurgerBlock = ({ id, title, price, imageUrl, types, sizes }) => {
  // Active type selection
  const typeName = ['traditional', 'with sesame'];
  const [activeType, setActiveType] = React.useState(0);

  // Active size selection
  const [activeSize, setActiveSize] = React.useState(0);

  //Add items to cart
  const dispatch = useDispatch();
  const cartItem = useSelector((state) => state.cartSlice.items.find((obj) => obj.id === id));
  const addedCount = cartItem ? cartItem.count : 0;

  const onClickAdd = () => {
    const item = {
      id,
      title,
      price,
      imageUrl,
      type: typeName[activeType],
      size: sizes[activeSize],
    };
    dispatch(addItems(item));
  };

  return (
    <div className="burger-block-wrapper">
      <div className="burger-block">
        <img className="burger-block__image" src={imageUrl} alt="Burger" />
        <h4 className="burger-block__title">{title}</h4>
        <div className="burger-block__selector">
          <p>type of bun</p>
          <ul>
            {types.map((typeId, i) => (
              <li
                onClick={() => setActiveType(i)}
                key={typeId}
                className={activeType === i ? 'active' : ''}
              >
                {typeName[typeId]}
              </li>
            ))}
          </ul>
          <ul>
            {sizes.map((size, i) => (
              <li
                onClick={() => setActiveSize(i)}
                className={activeSize === i ? 'active' : ''}
                key={i}
              >
                {size}
              </li>
            ))}
          </ul>
          <p>cutlet count</p>
        </div>

        <div className="burger-block__bottom">
          <div className="burger-block__price">from {price} $</div>
          <button onClick={onClickAdd} className="button button--outline button--add">
            <svg
              width="12"
              height="12"
              viewBox="0 0 12 12"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M10.8 4.8H7.2V1.2C7.2 0.5373 6.6627 0 6 0C5.3373 0 4.8 0.5373 4.8 1.2V4.8H1.2C0.5373 4.8 0 5.3373 0 6C0 6.6627 0.5373 7.2 1.2 7.2H4.8V10.8C4.8 11.4627 5.3373 12 6 12C6.6627 12 7.2 11.4627 7.2 10.8V7.2H10.8C11.4627 7.2 12 6.6627 12 6C12 5.3373 11.4627 4.8 10.8 4.8Z"
                fill="white"
              />
            </svg>
            <span>Add</span>
            {addedCount > 0 && <i>{addedCount}</i>}
          </button>
        </div>
      </div>
    </div>
  );
};

export default BurgerBlock;
